<template>
    <el-container>
        <el-aside class="left-group-aside" v-show="$getUserGroupSee()">
            <leftgroup
                :grouplist="groupCounts"
                @callGroup="callbackGroup"
            ></leftgroup>
        </el-aside>
        <el-main>
            <!--工具条-->
            <toolbar
                :buttonList="buttonList"
                @callFunction="callFunction"
            ></toolbar>

            <!--列表-->
            <el-table
                :data="users"
                highlight-current-row
                v-loading="listLoading"
                :row-class-name="$tableRowClassName"
                style="width: 100%"
            >
                <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="LoginName"
                    label="登录名"
                    width=""
                    sortable
                >
                </el-table-column>
                <el-table-column
                    prop="UserName"
                    label="用户姓名"
                    width=""
                    sortable
                >
                </el-table-column>
                <el-table-column
                    prop="Mobile"
                    label="手机号码"
                    width=""
                    sortable
                >
                </el-table-column>
                <el-table-column prop="RoleNames" label="角色" width="">
                    <template slot-scope="scope">
                        <el-tag
                            v-for="item in scope.row.RoleNames"
                            :key="item.Id"
                            >{{ item }}</el-tag
                        >
                    </template>
                </el-table-column>
                <el-table-column
                    prop="CreateTime"
                    label="创建时间"
                    width=""
                    sortable
                >
                </el-table-column>
                <el-table-column
                    label="操作"
                    width="150"
                    v-if="canEdit || canDelete"
                >
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="small"
                            @click="handleEdit(scope.$index, scope.row)"
                            >编辑</el-button
                        >
                        <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="small"
                            :key="scope.row.Id"
                            @click="handleDel(scope.$index, scope.row)"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>

            <!--编辑界面-->
            <el-dialog
                title="编辑"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="editForm"
                    label-width="100px"
                    :rules="editFormRules"
                    ref="editForm"
                >
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="登录名" prop="LoginName">
                                <el-input
                                    v-model.trim="editForm.LoginName"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="密码" prop="Password">
                                <el-input
                                    v-model.trim="editForm.Password"
                                    show-password
                                    auto-complete="off"
                                    clearable
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12"
                            ><el-form-item label="用户姓名" prop="UserName">
                                <el-input
                                    v-model.trim="editForm.UserName"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="手机号码" prop="Mobile">
                                <el-input
                                    v-model.trim="editForm.Mobile"
                                    maxlength="11"
                                    type="tel"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12" v-if="globalUserGroupid == 0">
                            <el-form-item label="所属分组" prop="GroupId">
                                <el-select
                                    class="selectrole"
                                    v-model="editForm.GroupId"
                                    clearable
                                    filterable
                                    placeholder="请选择"
                                    @change="groupChange"
                                >
                                    <el-option
                                        label="全部分组"
                                        :value="0"
                                    ></el-option>
                                    <el-option
                                        v-for="item in groups"
                                        :key="item.GROUPID"
                                        :label="item.groupshortname"
                                        :value="Number(item.GROUPID)"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12"
                            ><el-form-item label="角色" prop="RIDs">
                                <el-select
                                    class="selectrole"
                                    multiple
                                    v-model="editForm.RIDs"
                                    placeholder="请选择角色"
                                >
                                    <el-option
                                        v-for="item in showRoles"
                                        :key="item.Id"
                                        :label="item.Name"
                                        :value="item.Id"
                                    ></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="性别">
                                <el-radio-group v-model="editForm.Gender">
                                    <el-radio class="radio" label="1"
                                        >男</el-radio
                                    >
                                    <el-radio class="radio" label="2"
                                        >女</el-radio
                                    >
                                </el-radio-group>
                            </el-form-item></el-col
                        >
                        <el-col :span="12">
                            <el-form-item label="显示监测" prop="IsShowinsite">
                                <el-radio-group v-model="editForm.IsShowinsite">
                                    <el-radio class="radio" label="True"
                                        >是</el-radio
                                    >
                                    <el-radio class="radio" label="False"
                                        >否</el-radio
                                    >
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="单位名称" prop="Company">
                                <el-input
                                    v-model="editForm.Company"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="职位" prop="Position">
                                <el-input
                                    v-model="editForm.Position"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item> </el-col
                    ></el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="cancelEditDialog">取消</el-button>
                    <el-button
                        type="primary"
                        @click.native="editSubmit"
                        :loading="editLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>

            <!--新增界面-->
            <el-dialog
                title="新增"
                :visible.sync="addFormVisible"
                v-model="addFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="addForm"
                    label-width="100px"
                    :rules="addFormRules"
                    ref="addForm"
                >
                    <el-row>
                        <el-col :span="12"
                            ><el-form-item label="登录名" prop="LoginName">
                                <el-input
                                    v-model.trim="addForm.LoginName"
                                    auto-complete="off"
                                ></el-input> </el-form-item
                        ></el-col>
                        <el-col :span="12"
                            ><el-form-item label="密码" prop="Password">
                                <el-input
                                    v-model.trim="addForm.Password"
                                    show-password
                                    auto-complete="off"
                                ></el-input> </el-form-item
                        ></el-col>
                        <el-col :span="12">
                            <el-form-item label="姓名" prop="UserName">
                                <el-input
                                    v-model.trim="addForm.UserName"
                                    auto-complete="off"
                                ></el-input> </el-form-item
                        ></el-col>
                        <el-col :span="12">
                            <el-form-item label="手机号码" prop="Mobile">
                                <el-input
                                    v-model.trim="addForm.Mobile"
                                    maxlength="11"
                                    type="tel"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12" v-if="globalUserGroupid == 0">
                            <el-form-item label="所属分组" prop="GroupId">
                                <el-select
                                    class="selectrole"
                                    v-model="addForm.GroupId"
                                    clearable
                                    filterable
                                    placeholder="请选择"
                                    @change="groupChange"
                                >
                                    <el-option
                                        label="全部分组"
                                        :value="0"
                                    ></el-option>
                                    <el-option
                                        v-for="item in groups"
                                        :key="item.GROUPID"
                                        :label="item.groupshortname"
                                        :value="item.GROUPID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="角色" prop="RIDs">
                                <el-select
                                    class="selectrole"
                                    multiple
                                    v-model="addForm.RIDs"
                                    placeholder="请选择角色"
                                >
                                    <el-option
                                        v-for="item in showRoles"
                                        :key="item.Id"
                                        :label="item.Name"
                                        :value="item.Id"
                                    ></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="性别">
                                <el-radio-group v-model="addForm.Gender">
                                    <el-radio class="radio" :label="1"
                                        >男</el-radio
                                    >
                                    <el-radio class="radio" :label="2"
                                        >女</el-radio
                                    >
                                </el-radio-group>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12">
                            <el-form-item label="显示监测" prop="IsShowinsite">
                                <el-radio-group v-model="addForm.IsShowinsite">
                                    <el-radio class="radio" label="True"
                                        >是</el-radio
                                    >
                                    <el-radio class="radio" label="False"
                                        >否</el-radio
                                    >
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="单位名称" prop="Company">
                                <el-input
                                    v-model="addForm.Company"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="职位" prop="Position">
                                <el-input
                                    v-model="addForm.Position"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="cancelAddDialog">取消</el-button>
                    <el-button
                        type="primary"
                        @click.native="addSubmit"
                        :loading="addLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog></el-main
        >
    </el-container>
</template>

<script>
import util from "../../../util/date";
import {
    getUserListPage,
    getRoleAll,
    removeUser,
    editUser,
    addUser,
    getUserGroup,
} from "../../api/api";
import leftgroup from "../../components/LeftGroup";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
export default {
    components: { Toolbar, leftgroup },
    mixins: [dicmixin],
    data() {
        return {
            filters: {
                name: "",
            },
            groupCounts: [],
            selectGroupId: 0,
            users: [],
            roles: [],
            showRoles: [],
            total: 0,
            buttonList: [],
            page: 1,
            pageSize: 50,
            listLoading: false,

            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                LoginName: [
                    {
                        required: true,
                        message: "请输入登录名",
                        trigger: "blur",
                    },
                ],
                UserName: [
                    {
                        required: true,
                        message: "请输入用户姓名",
                        trigger: "blur",
                    },
                ],
                Mobile: [
                    {
                        required: true,
                        message: "请输入手机号码",
                        trigger: "blur",
                    },
                    {
                        validator: this.checkMobile,
                        message: "请输入合法的手机号码",
                    },
                ],
                GroupId: [
                    { required: true, message: "请选择分组", trigger: "blur" },
                ],
                RIDs: [
                    { required: true, message: "请选择角色", trigger: "blur" },
                ],
            },
            //编辑界面数据
            editForm: {
                id: 0,
                LoginName: "",
                UserName: "",
                Gender: -1,
                RIDs: [],
                Mobile: "",
            },

            addFormVisible: false, //新增界面是否显示
            addLoading: false,
            addFormRules: {
                LoginName: [
                    {
                        required: true,
                        message: "请输入登录名",
                        trigger: "blur",
                    },
                ],
                UserName: [
                    {
                        required: true,
                        message: "请输入用户姓名",
                        trigger: "blur",
                    },
                ],
                Password: [
                    { required: true, message: "请输入密码", trigger: "blur" },
                ],
                Mobile: [
                    {
                        required: true,
                        message: "请输入手机号码",
                        trigger: "blur",
                    },
                    {
                        validator: this.checkMobile,
                        message: "请输入合法的手机号码",
                    },
                ],
                GroupId: [
                    { required: true, message: "请选择分组", trigger: "blur" },
                ],
                RIDs: [
                    { required: true, message: "请选择角色", trigger: "blur" },
                ],
            },
            //新增界面数据
            addForm: {
                name: "",
                LoginName: "",
                UserName: "",
                Password: "",
                Gender: -1,
                RIDs: [],
                Mobile: "",
            },

            canEdit: false,
            canDelete: false,
            groups: [],
            globalUserGroupid: sessionStorage.user
                ? JSON.parse(sessionStorage.user).GroupId
                : 0,
        };
    },
    methods: {
        // 自定义校验规则
        checkMobile(rule, value, callback) {
            const r = /^1(3|4|5|6|7|8|9)\d{9}$/;
            if (value == null || String(value).trim() === "") {
                return callback(new Error("请输入正确的手机号码"));
            } else if (!r.test(value)) {
                return callback(new Error("请输入正确的手机号码"));
            } else {
                return callback();
            }
        },
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        //性别显示转换
        formatSex: function (row, column) {
            return row.Gender == 1 ? "男" : row.Gender == 2 ? "女" : "未知";
        },
        formatBirth: function (row, column) {
            return !row.birth || row.birth == ""
                ? ""
                : util.formatDate.format(
                      new Date(row.birth.replace(/-/g, "/")),
                      "yyyy-MM-dd"
                  );
        },
        handleCurrentChange(val) {
            this.page = val;
            this.getUsers();
        },
        //获取用户列表
        getUsers() {
            let para = {
                groupid: this.selectGroupId,
                page: this.page,
                size: this.pageSize,
                keyword: this.filters.name,
            };
            this.listLoading = true;

            getUserListPage(para).then((res) => {
                this.total = res.data.dataCount;
                this.users = res.data.data;
                this.listLoading = false;

                //NProgress.done();
            });
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { id: row.Id };
                    removeUser(para).then((res) => {
                        if (util.isEmt.format(res)) {
                            this.listLoading = false;
                            return;
                        }
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getUsers();
                        this.getGroupCounts();
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }
            if (row.GroupId) {
                this.showRoles = this.roles.filter((res) => {
                    return res.GROUPID == row.GroupId;
                });
            }

            this.editFormVisible = true;
            row.Password = "        ";
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.addFormVisible = true;
            this.addForm = {
                LoginName: "",
                UserName: "",
                Password: "",
                name: "",
                Gender: -1,
            };
            if (this.globalUserGroupid > 0) {
                this.addForm.GROUPID = this.globalUserGroupid;
            }
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.editLoading = true;
                    //NProgress.start();
                    let para = Object.assign({}, this.editForm);
                    editUser(para).then((res) => {
                        this.editLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });
                            this.cancelEditDialog();
                            this.getUsers();
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                    });
                    //});
                }
            });
        },
        //新增
        addSubmit: function () {
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.addLoading = true;
                    //NProgress.start();
                    let para = Object.assign({}, this.addForm);
                    addUser(para).then((res) => {
                        this.addLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "新增成功",
                                type: "success",
                            });
                            this.cancelAddDialog();
                            this.getUsers();
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                    });
                    //});
                }
            });
        },
        cancelAddDialog() {
            this.addFormVisible = false;
            this.$refs["addForm"].resetFields();
        },
        cancelEditDialog() {
            this.editFormVisible = false;
            this.$refs["editForm"].resetFields();
        },
        groupChange(val) {
            this.$set(this.addForm, "RIDs", []);
            this.$set(this.editForm, "RIDs", []);
            this.showRoles = this.roles.filter((res) => {
                return res.GROUPID == val;
            });
        },
        getGroupCounts() {
            getUserGroup().then((res) => {
                if (res.success) {
                    this.groupCounts = res.data;
                }
            });
        },
        callbackGroup(item) {
            this.selectGroupId = item.GROUPID;
            this.page = 1;
            this.getUsers();
        },
    },
    mounted() {
        this.getUsers();
        getRoleAll().then((res) => {
            this.roles = this.showRoles = res.data;
        });
        //this.getGroups();
        this.getGroupCounts();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>

<style scoped>
.selectrole {
    width: 100%;
}
.el-tag {
    margin-right: 5px;
    margin-bottom: 5px;
}
</style>



